.landing-pro {
  .list--plans {
    @extend %list;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 5em 0; }

  [class^="plan--"] {
    flex: 1 0 235px;
    padding: .5em 1.5em 3em;
    margin-bottom: 2em;
    border: {
      width: 2px;
      color: rgba($cement-grey, .2);
      style: solid; }

    @media #{$medium-up} {
      flex: 0 0 235px;
      margin-bottom: 0; }

    .list--check {
      margin: 3em 0;
      font-size: 16px;
      li {
        line-height: 2;
        &[data-jobs]:before {
          content: attr(data-jobs);
          width: 1.2em;
          background-image: none;
          line-height: 1;
          font-weight: 600;
          text-align: center;
          font-size: 18px; } } } }

  @mixin colorPlans($color, $hover-color, $bg: true) {
    border-top-color: $color;
    .plan-title {
      color: $color; }
    .plan-cta-btn {
      border: $color solid 1px;
      @if $bg {
        background-color: $color;
        color: white; }
      @else {
        color: $color;
        background-color: white; }
      &:hover {
        border-color: $hover-color;
        background-color: $hover-color; } }
    .list--check li:before {
      color: $color; } }

  .plan--red {
    @include colorPlans($brick-red, lighten($brick-red, 40), false);
    .list--check li:before {
      background-image: url("data:image/svg+xml;base64,"); } }
  .plan--green {
    @include colorPlans($turf-green, lighten($turf-green, 10));
    .list--check li:before {
      background-image: url("data:image/svg+xml;base64,"); } }
  .plan--teal {
    @include colorPlans($oxide-blue, lighten($oxide-blue, 10));
    .list--check li:before {
      background-image: url("data:image/svg+xml;base64,"); } }
  .plan--yellow {
    @include colorPlans($dozer-yellow, lighten($dozer-yellow, 10));
    .list--check li:before {
      background-image: url("data:image/svg+xml;base64,"); } }


  .plan-title,
  .plan-pricing,
  .plan-cta {
    text-align: center; }

  .plan-title {
    font-size: 1.9em;
    font-weight: 300; }

  .plan-pricing {
    .price {
      font-size: 20px; }
    .timeframe {
      font-size: 16px; } }

  .plan-cta-btn {
    display: inline-block;
    padding: 0.4em 0.9em;
    border-radius: 2px;
    font-size: 22px;
    text-decoration: none;
    transition: background-color 200ms ease, border-color 200ms ease; }

  .section--enterprise {
    .inner {
      @media #{$medium-up} {
        display: flex;
        justify-content: space-between;
        flex-flow: row wrap; } }

    .section-text {
      text-align: center;
      @media #{$medium-up} {
        text-align: right; } }

    .section-image {
      img {
        display: block;
        width: 320px;
        height: auto;
        margin: auto; }
      @media #{$medium-up} {
        img {
          width: 100%; } } }

    .section-text,
    .section-image {
      flex: 0 0 47%; } }

  .list--faq {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding: 0;
    margin: 3em 0 0;
    list-style: none;
    li {
      flex: 0 0 100%;
      padding: 1em 1.5em 1.7em;
      margin-bottom: 2em;
      border: solid 1px rgba($cement-grey, .2);
      @media #{$medium-up} {
        flex: 0 0 48%; } } }

  .faq-text {
    @include linkStyle;
    margin: 0;
    font-size: 16px;
    a {
      display: inline-block; } } }
