.getting-started {
  @include linkStyle;
  max-width: 900px;
  margin: auto;
  h1 {
    color: #808080;
    font-size: 36px;
    margin-bottom: 1em;
    font-weight: 300;
    line-height: 1.1; }
  h2 {
    color: #35a764;
    font-size: 22px;
    margin-bottom: 1em;
    font-weight: 300; }
  p, li {
    color: #666;
    font-size: $font-size-m;
    line-height: 25px; } }

.getting-started-steps {
  @include resetul;

  h2:before {
    width: 1.5em;
    height: 1.5em;
    margin-right: .5em;
    font-size: $font-size-m;
    background-color: #35a764;
    color: white;
    display: inline-block;
    border-radius: 50%;
    text-align: center;
    vertical-align: top; }
  li:nth-child(1) {
    h2:before {
      content: "1"; } }
  li:nth-child(2) {
    h2:before {
      content: "2"; } }
  li:nth-child(3) {
    h2:before {
      content: "3"; } }

  > li {
    margin-bottom: 5rem; }
  ul {
    @include resetul;
    li {
      padding-left: .9em;
      &:before {
        content: "\2022";
        display: inline-block;
        margin-right: .3em;
        margin-left: -.7em;
        font-size: 20px; } } }

  .media-elem {
    text-align: center; } }

.note {
  padding: .4em .5em;
  font-size: $font-size-m;
  border-radius: 4px;
  font-style: italic;
  code {
    display: inline-block;
    margin: 0 0 0 .3em;
    padding: 0em 0.4em;
    border: 1px solid;
    border-radius: 3px;
    font-family: $font-family-monospace;
    font-size: 11px;
    font-style: normal;
    line-height: 1.7; }
  strong {
    text-transform: uppercase;
    font-style: normal; } }

.note--info {
  background-color: #e7eeee;
  color: #3e8987;
  code {
    color: #267c8d;
    background-color: white; } }
