.sync-button {
  vertical-align: middle;
  display: flex;
  flex-flow: row-reverse wrap;
  position: relative;
  .button {
    position: relative;
    z-index: 2;
    height: 2.6rem;
    padding: 0.6em 0.8em;
    background-color: $agate-blue;
    border: none;
    border-radius: 2px;
    font-size: 14px;
    &:hover {
      background-color: $oxide-blue; } }
  .sync-last {
    position: absolute;
    left: -15em;
    top: 0;
    z-index: -1;
    transition: transform 150ms ease, opacity 250ms ease;
    transform: translateX(80%);
    opacity: 0;
    background: white; }
  .button:hover ~ .sync-last {
    transform: translateX(0);
    opacity: 1;
    z-index: 1; }

  .is-syncing {
    background-color: $oxide-blue; }

  .icon-trigger {
    @extend %icon-line-trigger-white;
    @extend .icon;
    margin-right: 0.3rem; }

  .loading-indicator--white {
    vertical-align: top;
    margin-right: .5em; } }

.sync-last {
  display: inline-block;
  margin: 0 1em 0;
  color: $asphalt-grey;
  text-align: right;
  line-height: 2.6; }
