.tabnav {
  color: $asphalt-grey;
  font-size: $font-size-sm;
  ul {
    @include resetul;
    margin-top: 2em;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    width: 100%;
    @media #{$medium-up} {
      margin-top: 0;
      margin-bottom: 4px;
      display: block; } }
  li {
    flex: 0 0 8em;
    text-align: center;
    margin: .5em 0;
    @media #{$medium-up} {
      display: inline-block;
      text-align: left;
      margin: 0 1.5em 0 0; } }

  a {}
  //  @extend %inline-block
  //width: 100%
  //color: $asphalt-grey
  //padding: 3px 0 5px

  .active a,
  a:hover {
    position: relative;
    color: $oxide-blue;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 100%;
      height: 2px;
      background-color: $oxide-blue; } }
  .active a {
    font-weight: $font-weight-bold; } }

.tabbody {
  position: relative;
  .tabnav-sub {
    @include resetul; }
