.tabs--main {
  #tab_build,
  #tab_job,
  #tab_request,
  #tab_requests,
  #tab_caches,
  #tab_settings {
    display: none;
    &.active {
      display: inline-block; } } }

#tab_build {
  position: relative;
  @media #{$medium-up} {
    left: .5em; }

  .tab-arrow {
    @extend %icon-line-arrowtabs;
    display: inline-block;
    position: absolute;
    top: 1px;
    left: -22px;
    width: 1em;
    height:  1.3em;
    background: {
      size: auto 100%;
      repeat: no-repeat; } } }
