@mixin buildTileColors($color, $bg, $status, $extra-hover: $color) {
  > .#{$status} {
    background-color: $bg;
    .status-icon g > * {
      stroke: $color; }
    &:hover {
      background-color: $extra-hover;
      .status-icon g > * {
        stroke: white; }
      .status-icon .circle {
        border-color: white;
        border-width: 2px; } } } }

.build-tiles {
  height: 4.7em;
  margin: 0;
  padding: 0;
  clear: both;
  list-style: none;
  li {
    position: relative;
    height: 100%;
    float: left;
    width: 20%;
    background-color: #F7F7F7;
    border-right: 1px solid white;
    &:last-of-type {
      border-right: none; }
    .status-icon {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 18px;
      height: 18px;
      margin: auto !important;
      background-color: transparent;
      transition: top 200ms ease;
      .is-rotating {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;
        width: 1.3em;
        height: 1.1em; }
      .circle {
        width: 8px;
        height: 8px; } }
    .build-tile-number {
      margin: auto;
      height: 1em;
      width: auto;
      text-align: center;
      position: absolute;
      transform: scale(.7);
      opacity: 0;
      color: white;
      transition: transform 200ms ease,  opacity 200ms ease;
      top: 0; right: 0; bottom: -1.5em; left: 0; }
    &:hover {
      .status-icon {
        top: -1.4em; }
      .build-tile-number {
        transform: scale(1);
        opacity: 1; } } }
  a {
    display: block;
    height: 100%;
    text-align: center; }

  @include buildTileColors($turf-green, $seed-green, 'passed');
  @include buildTileColors($brick-red, $quartz-red, 'failed');
  @include buildTileColors($brick-red, $quartz-red, 'errored');
  @include buildTileColors($cement-grey, $pebble-grey, 'canceled');
  @include buildTileColors($dozer-yellow, $haze-yellow, 'started');
  @include buildTileColors($dozer-yellow, $haze-yellow, 'queued');
  @include buildTileColors($dozer-yellow, $haze-yellow, 'booting');
  @include buildTileColors($dozer-yellow, $haze-yellow, 'received');
  @include buildTileColors($dozer-yellow, $haze-yellow, 'created'); }
