.log {
  position: relative;
  margin-top: 35px;

  .log-tail {
    z-index: 80;
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    margin: 10px 10px 0 0;

    .tail-label {
      display: none;
      padding: 0.1em 0.5em 0.1em 1.5em;
      cursor: pointer;
      background: {
        color: $cement-grey;
        repeat: no-repeat;
        size: auto 12px;
        position: 6px 5px; }
      border-radius: 2px;
      color: $pebble-grey;
      @extend %icon-arrow-down; }

    &:hover {
      .tail-label {
        display: inline-block; }
      .tail-status {
        display: none; } }

    &.scrolling {
      position: fixed;
      right: 32px; }

    &.bottom {
      bottom: 45px;
      top: inherit; }

    .tail-status {
      position: relative;
      display: inline-block;
      height: 20px;
      width: 20px;
      vertical-align: middle;
      background-color: $cement-grey;
      border-radius: 50%;
      &:after {
        content: "";
        display: block;
        height: 10px;
        width: 10px;
        background: #f1f1f1;
        border-radius: 50%;
        @extend %absolute-center; } }

    &.active .tail-status {
      background-color: #6b0; } }

  .to-top {
    z-index: 80;
    position: absolute;
    display: block;
    bottom: 2px;
    right: 2px;

    margin-right: 2px;
    padding-right: 16px;
    text-align: right;
    color: #999;
    background: inline-image('ui/workers-close.svg') no-repeat right 6px; } }

.log-header {
  height: 44px;
  margin: 0;
  padding: .7em .8em .6em;
  text-align: right;
  background-color: $log-header-bg; }
