.log-main {
  display: none;
  &.is-visible {
    display: block; }
  @media #{$medium-up} {
    display: block; } }

[class^="toggle-log-button"] {
  .icon-eye {
    vertical-align: middle;
    display: inline-block; }
  @media #{$medium-up} {
    display: none !important; } }

.toggle-log-button {
  border: 1px solid #f1f1f1;
  padding: .3em 1em;
  border-radius: 2px;
  display: block;
  color: #8d8d8d;
  text-align: center;
  .icon-eye {
    height: 1.8em;
    path {
      fill: #949899; }
    circle {
      stroke: #949899;
      fill: #fff; } }
  &.hidden {
    display: none; }
  &:hover,
  &:active {
    border-color: $oxide-blue;
    color: $oxide-blue;
    .icon-eye {
      path {
        fill: $oxide-blue; }
      circle {
        stroke: $oxide-blue;
        fill: transparent; } } } }

.toggle-log-button--dark {
  @extend %log-button;
  .icon-eye {
    margin-right: .3em;
    path {
      fill: #f1f1f1; }
    circle {
      stroke: #f1f1f1;
      fill: #f1f1f1; } }
  &:hover,
  &:active {
    background-color: #999a98; } }

.log-body {
  position: relative;

  pre {
    clear: left;
    min-height: 42px;
    padding: 15px 0;
    color: $pebble-grey;
    font-family: $font-family-monospace;
    font-size: $font-size-log;
    line-height: $line-height-log;
    white-space: pre-wrap;
    word-wrap: break-word;
    background-color: $color-bg-log-fold;
    counter-reset: line-numbering;
    margin-top: 0; }

  .cut {
    padding: 20px 15px 0 55px; }

  p {
    position: relative;
    padding: 0 15px 0 55px;
    margin: 0;
    min-height: 16px;
    &:hover {
      background-color: $color-bg-log-hover !important; }
    &.highlight {
      background-color: $color-bg-log-highlight;
      a {
        color: white; } }
    a {
      display: inline-block;
      text-align: right;
      min-width: 40px;
      margin-left: -33px;
      cursor: pointer;
      text-decoration: none;

      &::before {
        content: counter(line-numbering);
        counter-increment: line-numbering;
        padding-right: 1em; } } }

  .fold {
    position: relative;
    height: 16px;
    overflow: hidden;
    cursor: pointer;
    &.open {
      height: auto; }

    p:first-of-type {
      padding-right: 190px; }

    // &.active
    p:first-of-type {
      background: darken($color-bg-log-hover, 10) inline-image('ui/log.fold.open.2.svg') no-repeat 8px 3px;
      &.highlight {
        background-color: $color-bg-log-fold-highlight; } }

    &:not(.open) p:first-of-type {
      visibility: visible;
      height: auto;
      min-height: 16px;
      background-image: inline-image('ui/log.fold.closed.3.svg'); }

    .fold-name {
      position: absolute;
      z-index: 1;
      display: block;
      top: 2px;
      right: 85px;
      padding: 0px 7px 2px 7px;
      line-height: 10px;
      font-size: 10px;
      background-color: #666;
      border-radius: 6px;
      color: #bbb; } }

  .fold-start:not(.fold),
  .fold-end {
    display: none; }

  .duration {
    position: absolute;
    display: block;
    top: 2px;
    right: 12px;
    padding: 0px 7px 2px 7px;
    line-height: 10px;
    font-size: 10px;
    background-color: #666;
    border-radius: 6px;
    color: #bbb; }

  .loading {
    padding: 25px 0 0 10px; } }

.log-notice {
  background-color: #A6ADAD;
  color: white;
  min-height: 70px;
  line-height: 35px;
  text-align: center;
  border-radius: 4px;
  @media #{$medium-up} {
    height: 70px;
    line-height: 70px; } }

.log-container.hidden {
  display: none; }
