/***************
 * Styles specific for the ember-paper demo site
 ***************/
@import 'prism';
@import 'codeinline';

/* DEMO PAGE STYLES */
.doc-content {
  max-width: 864px;
  margin: 16px;
}

.doc-code-example {
  /* Initially we don't want any height, and we want the contents to be hidden */
  max-height: 0;
  overflow: hidden;

  /* Set our transitions up. */
  -webkit-transition: max-height 1s;
  -moz-transition: max-height 1s;
  transition: max-height 1s;

  /* On hover, set the max-height to something large. In this case there's an obvious limit. */
  &.is-visible {
    max-height: 400px;
    overflow-y: scroll;
  }
  pre {
    margin-top: 0;
  }
}

.doc-content-example {
  position: relative;
}

.list-demo .face {
  border-radius: 30px;
  border: 1px solid #ddd;
  width: 48px;
  margin: 16px;
}

.list-controls-demo md-divider {
  margin-top: 10px;
  margin-bottom: 10px;
}


.icon-demo .icon-tile {
  float: left;
  min-width: 200px;
  text-align: center;
}

/* Inline code */
code.paper {
  color: #106CC8;
  margin-left: 1px;
  margin-right: 1px;
  -webkit-font-smoothing: auto;
  padding: 0.125em 0.35em;
  border-radius: 2px;

}

code.paper {
  font-size: 1.4rem;
  background: rgba(0, 0, 0, 0.065);
}

.grid-list-demo1 {
  md-grid-list {
    margin: 8px;
  }
  .gray {
    background: #f5f5f5;
  }
  .green {
    background: #b9f6ca;
  }
  .yellow {
    background: #ffff8d;
  }
  .blue {
    background: #84ffff;
  }
  .purple {
    background: #b388ff;
  }
  .red {
    background: #ff8a80;
  }

  md-grid-tile {
    transition: all 400ms ease-out 50ms;
  }
}

.card-demo {
  .card-media {
    background-color: #999999;
    height: 100%;
    width: 100%;
  }
}

.grid-list-demo-dynamicTiles {
  md-icon {
    width: 50%;
    height: 30%;
  }

  md-icon svg {
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
  }

  .s64 {
    font-size: 64px;
  }

  .s32 {
    font-size: 48px;
  }

  md-icon.fa {
    display: block;
    padding-left: 0;
  }

  md-icon.s32 span {
    padding-left: 8px;
  }

  md-grid-list {
    margin: 8px;
  }
  .gray {
    background: #f5f5f5;
  }
  .green {
    background: #b9f6ca;
  }
  .yellow {
    background: #ffff8d;
  }
  .blue {
    background: #84ffff;
  }
  .darkBlue {
    background: #80d8ff;
  }
  .deepBlue {
    background: #448aff;
  }
  .purple {
    background: #b388ff;
  }
  .lightPurple {
    background: #8c9eff;
  }
  .red {
    background: #ff8a80;
  }
  .pink {
    background: #ff80ab;
  }

  md-grid-tile {
    transition: all 300ms ease-out 50ms;
  }

  md-grid-tile md-icon {
    padding-bottom: 32px;
  }

  md-grid-tile md-grid-tile-footer {
    background: rgba(0, 0, 0, .68);
    height: 36px;

  }

  md-grid-tile-footer figcaption {
    width: 100%;
  }

  md-grid-tile-footer figcaption h3 {
    margin: 0;
    font-weight: 700;
    width: 100%;
    text-align: center;
  }

}

